import React from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { Routes } from 'modules';

//i18n
import { IntlProvider } from 'react-intl';
import En from 'i18n/en.json';

const App: React.FC = () => {
  return (
    <IntlProvider locale="en" messages={En}>
      <Router>
        <Routes />
      </Router>
    </IntlProvider>
  );
};

export default App;
