import React, { Suspense, useEffect } from 'react';
import { RouteComponentProps, Switch, Route, withRouter, Redirect, useLocation } from 'react-router-dom';
import { Header, Loader } from 'components';
import { API } from 'services/api';
import 'url-search-params-polyfill';

// import module styles
import './module.scss';
import { useQuery } from 'utils';

const ItemsOverview = React.lazy<any>(() => import('./itemsOverview/itemOverview'));
const ProofOfDelivery = React.lazy<any>(() => import('./proofOfDelivery/proofOfDeliveryRoute'));
const ProofOfCollection = React.lazy<any>(() => import('./proofOfCollection/proofOfCollectionRoute'));
const ReceiptPage = React.lazy<any>(() => import('./receiptPage/receiptPage'));
const FineryReceiptPage = React.lazy<any>(() => import('./fineryReceiptPage/receiptPage'));

interface Props extends RouteComponentProps { }

/**
 * Modules Entry Routes
 */
const Routes: React.FC<Props> = () => {
  let query = useQuery();

  // if token is present, set the auth header globally

  useEffect(() => {
    const token =
      '4775f2c6-dad5-4c62-9b45-30d398e86811~4d978c4a07c44b4b4fcf19b146fbb609f05440cb89dd55bde7ece54f19567ae960fac8e665a7b103dbdbb79580dbec764d80bbcd0f1fa1980595166f5990009e';
    if (token) API.defaults.headers.common['Authorization'] = `Bearer ${token}`;
    else {
      // redirect to washmen.com in case no token was found
      delete API.defaults.headers.common['Authorization'];
      window.location.href = 'https://washmen.com';
    }
  }, [query]);

  return (
    <Suspense fallback={<Loader show />}>
      <Switch>
        <Route exact path="/">
          <ProofOfDelivery />
        </Route>
        <Route exact path="/proofOfCollection">
          <ProofOfCollection />
        </Route>
        <Route exact path="/proofOfDelivery">
          <ProofOfDelivery />
        </Route>
        <Route exact path="/receipt">
          <Header />
          <ReceiptPage />
        </Route>
        <Route exact path="/finery/receipt">
          <FineryReceiptPage />
        </Route>
        <Redirect to="/" />
      </Switch>
    </Suspense>
  );
};
export default withRouter(Routes);
