import React from 'react';
import classNames from 'classnames';
import Alert from 'react-bootstrap/Alert';
import { ReactComponent as InfoWarning } from 'assets/images/icons/ic-info-orange.svg';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

interface Props {
  title?: string;
  type?: 'warning' | 'primary' | 'secondary' | 'success' | 'danger' | 'info' | 'dark' | 'light' | undefined;
}

const WmAlert: React.FC<Props> = ({ title = '', type = 'warning', children }) => (
  <Alert className={classNames('shadow-sm b-2 p-2 pl-3 pr-3 d-flex bg-primary-orange border-0')} variant={type}>
    <Row noGutters>
      <Col xs="auto" className="mr-2 ">
        <InfoWarning className={`${type}-icon-svg`} />
      </Col>
      <Col>
        <p style={{ fontFamily: 'Inter Semi Bold', fontSize: 14 }} className={classNames('mb-0 pb-0 pt-1', `text-${type}`)}>
          {title}
        </p>
        <p className={classNames('text-slate-gray font-small p-0 m-0 ml-0', { 'text-slate-gray': type === 'warning' })}>{children}</p>
      </Col>
    </Row>
  </Alert>
);

export default WmAlert;
